.experience-tab{
    background: #0C0C0C;
    padding: 3rem 3rem 18rem;
    flex-flow:column;
    box-sizing: border-box;
}

.tab-title{
    margin: 10px auto 40px;
    width: 60%;
    text-align: left;
    display: flex;
    position: relative;
    white-space: nowrap;
}

.tab-title::after {
    content: '';
    display: block;
    position: relative;
    top: 34px;
    width: 100%;
    height: 2px;
    margin-left: 20px;
    background-color: #fff;
}

.tab-container{
    display: flex;
    flex-flow: row;
    position: relative;
    max-width: 1120px;
    width: 60%;
    height: 100%;
    margin: 0 auto;
    background: #0C0C0C;
    /* word-break: break-all; */
    border: 1px solid (rgba0,0,0,0,0.274);
    box-sizing: border-box;
}

.bloc-tabs {
    width: 200px;
    height: 100%;
    display: flex;
    flex-flow: column;
}

.tabs {
    padding: 15px 15px;
    text-align: left;
    width: calc(100%-30px);
    min-width: 100px;
    height: 30px;
    background: #0C0C0C;
    color: #3b3b3b;
    cursor: pointer;
    border-bottom: 1px solid rgba(0, 0, 0, 0.274);
    box-sizing: content-box;
    position: relative;
    outline: none;
    font-family: 'PT Sans', sans-serif;
    transition: color .2s ease-in;
}

.tabs:hover{
    opacity: 1;
    background: #1C1C1C;
    color:#FFF;
}

.tabs:not(:last-child){
    border-right: 1px solid rgba(0, 0, 0, 0.274);
}

.active-tab  {
    background: #1C1C1C;
    color:#FFF;
    border-bottom: 1px solid transparent;
}

.tabs::after {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: -5px;
    transform: translateY(-50%);
    width: 1px;
    height: 102%;
    background: rgb(88, 241, 208);
}


.active-tab::after {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: -5px;
    transform: translateY(-50%);
    width: 2px;
    height: 100%;
    background: rgb(170, 62, 192);
}

button {
    border: none;
}

.content-tabs {
    flex-grow : 1;
    height: 300px;
    width: 100%;
}
.content {
    background: #0c0c0c;
    padding: 20px;
    width: 100%;
    height: 100%;
    display: none;
}
.content h2 {
    color: #FFF;
    padding: 0px 0 2px 0px;
}
.content h3 {
    color: #5a5a5a;
    font-size: 16px;
    padding: 0px 0 5px 0px;
}
.content hr {
    width: 100px;
    height: 2px;
    background: #FFF;
    margin-bottom: 5px;
}
.content p {
    padding: 10px 0 0 0;
    color: #9b9b9b;
}
.active-content {
    display: block;
}

@media (max-width: 720px) {
    .tab-container{
        flex-flow: column;
        width: 80%;
    }
    .bloc-tabs {
        width: 100%;
        height: 30%;
        flex-flow: row;
        overflow: auto;
    }
    .tabs {
        padding: 15px 15px;
        text-align: center;
        width: 100%;
        min-width: 100px;
        height: 30px;
        background: rgba(128, 128, 128, 0.075);
        cursor: pointer;
        border-bottom: 1px solid rgba(0, 0, 0, 0.274);
        box-sizing: content-box;
        position: relative;
        outline: none;
    }
    .tab-title{
        text-align: center;
        justify-content: center;
        width: 100%;
    }
    .tab-title::after{
        width: 0;
        height: 0;
    }
    .tabs::after {
        content: "";
        display: block;
        position: absolute;
        top: 5px;
        left: 50%;
        transform: translateX(-50%);
        height: 1px;
        width: 102%;
        background: rgb(88, 241, 208);
    }
    .active-tab::after {
        content: "";
        display: block;
        position: absolute;
        top: 5px;
        left: 50%;
        transform: translateX(-50%);
        height: 2px;
        width: 100%;
        background: rgb(170, 62, 192);
    }
}