.about-me{
    background-color: #0C0C0C;
    padding: 2rem 0 0;
    min-height: 100vh;
    box-sizing: border-box;
}

.bio-container{
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
}

.bio-wrapper{
    padding: 20px 30px 15px;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.bio-headshot{
    width: 300px;
    height: 300px;
    padding: 1rem;
    border-radius: 50%;
}

.bio-text{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 30px;
    text-align: left;
    color:#FFF;
    font-family: 'Montserrat', sans-serif;
    border-radius: 20px;
    z-index: 10;
}

.bio-link{
    text-decoration: none;
    color: #FFF;
}

.bio-text > h1{
    text-align: left;
    color:#FFF;
    font-size: 2rem;
    margin: 0 .8rem 0;
}
.bio-text > p{
    text-align: left;
    color:#FFF;
    margin: .8rem;
}

.bio-text > ul{
    margin: .8rem;
}

.skills-list{
    display: grid;

    grid-template-columns: repeat(2, minmax(140px, 200px));
    padding: 0;
    margin: 20px 0 0 0;
    overflow: hidden;
    list-style: none;
}

.skills-list > li{
    position: relative;
    margin-bottom: 10px;
    padding-left: 20px;
    font-size: .8em;
}

.skills-list > li::before{
    content: '▹';
    position: absolute;
    left: 0;
    color: #FFF;
    font-size: .8em;
    line-height: 12px;
}

.cursor{
    content:"";
    top:var(--y, 0);
    left:var(--x, 0);
    width:80px;
    height:80px;
    border:3px solid white;
    border-radius:50%;
    position: fixed;
    box-shadow: 2px -3px 41px -1px rgba(250,250,250,0.64);
    transition: all .1s linear; 
    pointer-events: none;
    transform: translate(-50%,-50%);
    z-index: 1;
  }

@media only screen and (max-width: 1024px) {
    .bio-wrapper {
        flex-direction: column;
    }
    .bio-text > h1{
        text-align: center;
    }
    .bio-text > p{
        text-align: center;
    }
    .skills-list{
        align-self: center;
    }
}