.footer-container {
    background-color: #242424;
    padding: 2rem 0 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    position: relative;
    z-index: 10;
}

.social-icon-link{
    color: #fff;
    font-size: 24px;
}

.social-media{
    width:100%;
}

.social-media-wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    margin: 0 10vw 0 10vw;
}

.social-icons{
    display: flex;
    justify-content: space-between;
    align-items:center;
    margin: 0 auto 0 auto;
    width: 320px;
}

.social-icon{
    width: 4vh;
    height: 4vh;
    filter: invert(1);
    transition: all .4s linear; 
    opacity: 0.6;
}

.social-icon:hover{
    opacity: 1;
}

.social-logo {
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-self: center;
    margin-bottom: 16px;
}
