.cards {
  padding: 4rem;
  background: #0C0C0C
}

h1 {
  text-align: center;
  color:#fff;
  font-size: 48px;
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 50px 0 45px;
  height: 100%;
  width: 100%;
}

.cards__items {
  margin-bottom: 24px;
}

.cards__item {
  width: 100%;
  height: 100%;
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 10px;
  background: #1C1C1C;
  z-index: 2;
}

.cards__item__pic-wrap > p{
  position: absolute;
  padding: 2.5rem 1.25rem;
  opacity: 0;
  height: 100%;
  width: 100%;
  background:rgb(60, 27, 66);
  color: #FFF;
  z-index: 3;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: opacity 0.2s;
  -webkit-transition: opacity 0.2s;
}

.cards__item:hover .cards__item__pic-wrap > p{
  opacity: 0.95;
}

.cards__item__link {
  position: relative;
  display: flex;
  flex-flow: column;
  width: 100%;
  height: 100%;
  /* box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17); */
  /* -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017)); */
  /* filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017)); */
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  height: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

