.cards__tags{
  display: grid;
  grid-template-columns: repeat(6, auto);
  grid-gap: 10px;
  width: 30%;
  padding: 10px;
}

.cards__tag::after{
  content: attr(data-category);
  position: relative;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: rgb(60, 27, 66);
  box-sizing: border-box;
  border-radius: 0.3rem;
  z-index: 4;
}

.cards__item:hover .cards__tag::after{
  background-color: #fff;
  color: rgb(60, 27, 66);
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards__item__img:hover {
  transform: scale(1.1);
}

.cards__item__info {
  padding: 20px 30px 20px;
}

.cards__item__text {
  color: #FFF;
  font-size: 18px;
  line-height: 24px;
}

.double__cards{
  font-size: 2vw;
}

.triple__cards{
  font-size: 1.4vw;
}

.cursor{
  content:"";
  top:var(--y, 0);
  left:var(--x, 0);
  width:80px;
  height:80px;
  border:3px solid white;
  border-radius:50%;
  position: fixed;
  box-shadow: 2px -3px 41px -1px rgba(250,250,250,0.64);
  transition: all .1s linear; 
  pointer-events: none;
  transform: translate(-50%,-50%);
  z-index: 1;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 84%;
  }
}

@media only screen and (min-width: 1024px) {
  .cards__items {
    display: flex;
  }
  
}

@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
  }
  .double__cards{
    font-size: 4vw;
  }
  .triple__cards{
    font-size: 4vw;
  }
}

@media (pointer: coarse), (pointer: none) {
  .cursor{
    display: none;
  }
}