
.hero-container {
    background: url('../images/bg-1.svg') center center/cover no-repeat;
    /* background: #000; */
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
}

.poptext{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
    text-decoration: none;
    z-index: 1;
}

.poptext > p {
    color: #fff;
    font-size: 32px;
    margin-top: 8px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    transition: all .4s linear; 
    opacity: 0.3;
}

.poptext:hover > p{
    opacity: 1;
}

.poptext > h1{
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
    -webkit-text-stroke: 2px #fff;
    -webkit-text-fill-color: transparent;
    color: transparent;
    transition: all .4s linear; 
    opacity: 0.3;
}

.poptext:hover > h1{
    opacity: 1;
    -webkit-text-fill-color: #fff;
    color: #fff;
}

.cursor{
    content:"";
    top:var(--y, 0);
    left:var(--x, 0);
    width:80px;
    height:80px;
    border:3px solid white;
    border-radius:50%;
    position: fixed;
    box-shadow: 2px -3px 41px -1px rgba(250,250,250,0.64);
    transition: all .1s linear; 
    pointer-events: none;
    transform: translate(-50%,-50%);
    z-index: 2;
}

.poptext:hover ~ .cursor{
    transform: translate(-50%,-50%) scale(1.8);
    mix-blend-mode: difference;
    background: #fff;
}

