.hero-btns{
    margin-top: 32px;
}

.hero-btns .btn {
    margin: 6px;
}

@media screen and (max-width: 960px){
    .hero-container > h1 {
        font-size: 70px;
        margin-top: -150px;
    }
}

@media screen and (max-width: 768px){
    .hero-container > h1 {
        font-size: 50px;
        margin-top: -100px;
    }

    .poptext > h1{
        font-size: 50px;
    }

    .hero-container > p {
        font-size: 30px;
    }

    .poptext > p {
        font-size: 30px;
    }

    .btn-mobile{
        display: block;
        text-decoration: none;
    }
    .btn{
        width: 100%;
    }
}

